.header {
  padding-top: 16px;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 100;
  width: 100%;

  @include big-desktop {
    padding-top: 10px;
  }

  @include tablet {
    padding-top: 20px;
  }

  @include small-tablet {
    padding-top: 10px;
  }

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__logo {
    display: flex;
    align-items: center;
  }

  &__logo-img {
    width: 141px;
    height: 50px;
    fill: var(--text-color);

    @include tablet {
      width: 94px;
      height: 34px;
    }
  }

  &__logo-text {
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    color: var(--text-color);
    max-width: 275px;
    display: inline-block;
    margin-left: 50px;
  }
}

.header-items {
  display: flex;

  &__link {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    font-weight: 500;
    font-size: 16px;
    line-height: 23px;
    color: var(--text-color);
    transition: opacity .3s ease-in ;

    &:hover {
      opacity: .7;
    }

    @include tablet {
      font-size: 14px;
      line-height: 17px;
    }

    @include small-tablet {
      font-size: 0;
      line-height: 0;
    }

    &:not(:first-child) {
      margin-left: 60px;

      @include tablet {
        margin-left: 35px;
      }

      @include small-tablet {
        margin-left: 10px;
      }
    }
  }

  &__img {
    width: 36px;
    height: 37px;
    fill: var(--img-color);

    @include tablet {
      width: 24px;
      height: 24px;
    }
  }

  &__count {
    display: inline-flex;
    align-items: center;
    padding: 0 4px;
    height: 10px;
    font-weight: 400;
    font-size: 9px;
    line-height: 1.1;
    background-color: var(--count-color);
    border-radius: 50%;
    color: var(--light-color);
    position: absolute;
    top: 5px;
    right: 20px;
    transition: opacity .3s ease-in ;

    @include tablet {
      height: 7px;
      padding: 0 2px;
      font-size: 5px;
    }

    @include small-tablet {
      right: 0;
    }

    &:hover {
      opacity: .7;
    }
  }
}

.home-body {
  & .header__logo-img {
    fill: var(--light-color);
  }

  & .header__logo-text {
    display: none;
  }

  & .header-items__link--search {
    display: none;
  }

  & .header-items__link--catalog {
    display: none;
  }

  & .header-items__link {
    color: var(--light-color);
    transition: opacity .3s ease-in ;

    &:hover {
      opacity: .7;
    }
  }

  & .header-items__img {
    fill: var(--light-color);
    transition: opacity .3s ease-in ;

    &:hover {
      opacity: .7;
    }
  }
}

.header--fixed {
  position: fixed;
  left: 0;
  animation: 1s forwards fixedMenu;

  &.header {
    background: rgba(255, 255, 255, 0.8);
    box-shadow: 0px 4px 70px rgba(0, 0, 24, 0.07);
    backdrop-filter: blur(20px);
    border-bottom-left-radius: 30px;
    border-bottom-right-radius: 30px;
  }

  & .header__logo-img {
    fill: var(--text-color);
  }

  & .header__logo-text {
    display: block;

    @include desctop {
      display: none;
    }
  }

  & .header-items__link--search {
    display: flex;
  }

  & .header-items__link--catalog {
    display: flex;
  }

  & .header-items__link {
    color: var(--text-color);
  }

  & .header-items__img {
    fill: var(--img-color);
  }
}

@-webkit-keyframes fixedMenu {
  0% {
    padding: 0
  }

  50% {
    padding: 16px 0
  }

  100% {
    padding: 16px 0
  }
}

.header-main {
  background: rgba(255, 255, 255, 0.8);
  box-shadow: 0px 4px 70px rgba(0, 0, 24, 0.07);
  backdrop-filter: blur(20px);
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
  padding-bottom: 25px;
  position: fixed;
  left: 0;

  @include desctop {
    & .header__logo-text {
      display: none;
    }
  }

  @include tablet {
    padding-top: 28px;

    .header-items__link:not(:first-child) {
      margin-left: 45px;
    }
  }

  @include small-tablet {
    padding-top: 13px;

    .header-items__link:not(:first-child) {
      margin-left: 25px;
    }
  }

  & .header__logo-img {
    fill: var(--text-color);
  }

  & .header-items__link--search {
    display: flex;
  }

  & .header-items__link--catalog {
    display: flex;
  }

  & .header-items__link {
    color: var(--text-color);

    @include mobile {
      &:nth-child(2) {
        display: none;
      }

      &:nth-child(4) {
        display: none;
      }
    }
  }

  & .header-items__img {
    fill: var(--img-color);
  }
}
